.preloader{
    background-color: transparent;
    overflow: hidden;
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-content: center;
    justify-content: flex-start;
    z-index: 100000;
    pointer-events: none;
}
.preloaderLine{
    height: 3px;
    width: 100%;
    background: #ddd;
}
.preloaderLine:before {
    content: '';
    display: block;
    background-color: #3E613F;
    height: 3px;
    width: 30%;
    animation: getWidth 1s ease-in infinite;
}

@keyframes getWidth {
    100% { transform: translateX(500%) }
}